<!--账户信息-->
<template>

  <div class="top">
    <van-nav-bar title="账户信息" left-arrow @click-left="onClickLeft"/>
    <div class="top1"></div>
   <div>
     <div class="ka">
       <van-uploader class="qing1">
         <van-button class="qing"  style="clear: both" is-link ><span class="ming">上传头像</span></van-button>
       </van-uploader>
       <van-cell style="height: 0.5rem;" title="用户名" is-link url="http://localhost:8080/#/user_name" />
     </div>
     <div><img id="tou" style="margin-left: 0.3rem" src="../assets/img/34.jpg"/></div>
     <van-icon name="arrow"  class="fei"/>
   </div>


    <van-cell style="height: 0.5rem;" title="收获地址" is-link url="//youzan.github.io/vant/mobile.html" />
    <div class="p1"><p>账号绑定</p></div>
<div class="qi">
  <van-cell style="height: 0.5rem;" title="手机" is-link @click="yes"/>

  <img class="fen" src="../assets/img/35.png"/>
  <div class="p1"><p>安全设置</p></div>

</div>
    <van-cell @click="xiugai" class="li" title="密码登录" style="height: 0.5rem; "  is-link value="修改 " />
    <div style="width: 100%;height: 0.3rem;background-color: rgb(245,245,245)"></div>
    <div class="dader"><van-button name="haha"  type="danger" size="large"  @click="haha">退出登陆</van-button></div>

    <div class="d1" v-if="show">
      <img class="chu" src="../assets/img/222.png"/>
      <h4>请在手机APP中设置</h4>
      <button class="butt" @click="no">确定</button>
    </div>
    <div class="d2" style="position: absolute;width: 90%;background-color: white;text-align: center;left: 0.2rem;border-radius: 0.1rem;height: 3rem;top: 1.2rem;" v-if="show1">
      <img style="width: 50%" class="chu" src="../assets/img/222.png"/>
      <h4 style="line-height: 0.9rem;font-size: 0.3rem;font-weight: 500">是否退出登录</h4>
      <button style="background-color: gray;border: 0;width: 30%;height: 0.4rem;margin-right: 0.1rem;border-radius: 0.1rem;color: white"@click="feng">再等等</button>
      <button style="background-color: red;border: 0;width: 30%;height: 0.4rem;margin-left: 0.1rem;border-radius: 0.1rem;color: white">退出登录</button>
    </div>
  </div>



</template>

<script>
    export default {
        name: "data_mi",
      data(){
          return{
          show:false,
            show1:false
        }
      },
      methods:{
        onClickLeft(){
          this.$router.push({path:"/spHome/home4"});
        },
        yes(){
          this.show=true;
        },
        no(){
          this.show=false;
        },
        haha(){
          this.show1=true;
        },
        feng(){
          this.show1=false;
        },
        xiugai(){
          this.$router.push("/reset")
        }
      }
    }
</script>

<style scoped>
  #head_top[data-v-cec0c0c0] {
    position: fixed;
    z-index: 100;
  }
  .fei{
    position: absolute;
    right: 0.15rem;
    top: 0.8rem;
    z-index: 1;
    font-size: 0.24rem;
  }
 .ming{
   margin-right: 2rem;
 }
 .qing1{
   width: 100%;
 }
  .qing{
    width: 100%;
    height: 0.7rem;
    font-size: 0.18rem;
    background-color: white;
    color: gray;
    border: 0.001rem solid white;

  }
  .van-nav-bar__title{
    color: white;
    font-weight: 700;
    font-size: 0.2rem;
  }
  .van-nav-bar{
    background-color: rgb(49,144,232);
