  width: 100%;
  height: 0.4rem;
  background-color: rgb(245,245,245);
  line-height: 0.4rem;

}
p{
  margin-left: 0.15rem;
}
  .van-cell span{
   font-size: 0.199rem;
    line-height: 0.5rem;
    color: gray;
  }
  #tou{
    border-radius: 50%;
    width: 0.6rem;
   position: absolute;
    z-index: 1;
    top: 0.6rem;
    left: 2.5rem;

  }
 .qi .van-cell span[data-v-4f5d381e]{
    margin-left: 0.2rem;
   position: relative;
  }
  .fen{
    position: absolute;
    left: 0.13rem;
    top: 2.79rem;
    width: 0.2rem;
    z-index: 1;
  }
  .li span{
    /*position: absolute;*/
    top: 0;
    font-size: 0.2rem;
    color: black;
    z-index: 2;
  }
  .van-cell span[data-v-4f5d381e] {
    font-size: 0.199rem;
     line-height: 0.3rem;
    color: gray;
  }
  van-button{
    background-color: red;
  }
  [data-v-4f5d381e]:before {
    color: gray;
    font-size: 0.2rem;
    font-weight: 700;
  }
  .ka [data-v-4f5d381e]:before{
    margin-top: 0.1rem;
  }
 .dader{
      text-align:center;
   background-color: rgb(245,245,245);

 }
  .van-button--large{
    width: 90%;
    height: 0.4rem;
    line-height: 0.4rem;
    border-radius: 0.1rem;
    background-color: rgb(216,88,74);
  }

  .d1{
    position: absolute;
    top: 2.3rem;
    left: 0.35rem;
    z-index: 2;
    background-color: white;
    width: 80%;
    height: 2rem;
    border-radius: 0.1rem;
    text-align: center;
  }
 .butt{
   width: 100%;
   height: 0.4rem;
   border-radius: 0px 0px 0.1rem 0.1rem;
   background-color: rgb(76,217,100);
   margin-bottom: 0px;
   border: 0px;
   font-size: 0.18rem;
   font-weight: 700;
   color: white;
   margin-top: 0.18rem;
 }
.chu{
  width: 50%;
}
  h4{
    margin-top: 0.15rem;
  }
</style>



