<template>
  <div class="spHome1">
    <div class="middle">
      <div class="one">
        <van-swipe :autoplay="5000" indicator-color="#3190e8" style="height: 100%;width: 100%;">
          <van-swipe-item>
            <div class="shop_fenlie" v-for="(once,k) in fenleiArr1" :key="k" @click="fenlie1(once.title,once.id)">
              <img :src="'https://fuss10.elemecdn.com'+once.image_url" alt="">
              <p>{{once.title}}</p>
            </div>
          </van-swipe-item>
          <van-swipe-item>
            <div class="shop_fenlie" v-for="(once,k) in fenleiArr2" :key="k" @click="fenlie2(once.title,once.id)">
              <img :src="'https://fuss10.elemecdn.com'+once.image_url" alt="">
              <p>{{once.title}}</p>
            </div>
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>
    <div>
      <div class="nearby_home">
        <div class="nearby">
          <span class="qyh span1">&#xe604;</span>
          <span class="span2">附近商家</span>
        </div>
        <div class="nearby_one" v-for="(once,key) in shoperArr" :key="key">
          <div class="nearby_one_img pull-left">
            <img :src="'http://elm.cangdu.org/img/'+once.image_path" alt="">
          </div>
          <div class="message pull-right">
            <div class="message_one">
              <span class="message_one_p1">品牌</span>
              <span class="message_one_p2">{{once.name}}</span>
              <span class="pull-right message_one_p3">保</span>
              <span class="pull-right message_one_p4">准</span>
              <span class="pull-right message_one_p5">票</span>
            </div>
            <div class="message_two clearfix">
              <div class="rate">
                <van-rate size="0.1rem" gutter="0" v-model="once.rating" readonly allow-half void-icon="star" void-color="#eee"/>
              </div>
              <span>{{once.rating}}</span>
              <span>月销{{once.recent_order_num}}单</span>
              <span class="pull-right">{{once.supports[1].name}}</span>
              <span class="pull-right">{{once.delivery_mode.text}}</span>
            </div>
            <div class="message_three clearfix">
              <p class="pull-left">¥{{once.float_minimum_order_amount}}起送<span>/</span>配送费约¥{{once.float_delivery_fee}}</p>
              <p class="pull-right">{{once.distance}}<span>/</span>{{once.order_lead_time}}</p>
            </div>
          </div>
          <div class="clearfix"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import tatle from '../../router/jianting';
    export default {
      name: "spHome1",
      data(){
        return {
          geohash:[],
          fenleiArr1:[],
          fenleiArr2:[],
          shoperArr:[],
          value:4.5,
        }
      },
      methods:{
        fenlie1(a,b){
          this.$router.push({path:'/fenlei',query:{name:a,id:b}});
        },
        fenlie2(a,b){
          this.$router.push({path:'/fenlei',query:{name:a,id:b}});
        }
      },
      created(){
        tatle.$emit('changecolor','外卖');
        this.geohash=localStorage.qyh_geohash.split(",");
        Vue.axios.get("https://elm.cangdu.org/v2/index_entry").then(result=>{
          this.fenleiArr1=result.data.splice(0,8);
          this.fenleiArr2=result.data;
        });
        Vue.axios.get("https://elm.cangdu.org/shopping/restaurants",{
          params:{
            latitude:this.geohash[0],
            longitude:this.geohash[1],
          }
        }).then(result=>{
          this.shoperArr=result.data;
        })
      },
    }
</script>

<style scoped lang="less">
  .spHome1{
    padding-bottom: 0.35rem;
  }
  .middle{
    padding-top: 0.48rem;
  }
  .nearby_one{
    font-size: 0.12rem;
  }
  .one{
    width: 100%;
    height: 2rem;
    background: white;
  }
  .shop_fenlie{
    width: 25%;
    height: 1rem;
    text-align: center;
    display: inline-block;
  }
  .shop_fenlie img{
    margin: 0.1rem;
    width: 50%;
  }
  .shop_fenlie p{
    font-size: 0.14rem;
    font-weight: 400;
  }
  .span1{
    font-size: 0.2rem;
    color: rgba(0,0,0,0.4);
    margin-left: 0.1rem;
  }
  .span2{
    font-size: 0.13rem;
    color: rgba(0,0,0,0.4);
  }
  .nearby{
    margin-top: 0.11rem;
    padding-top: 0.08rem;
  }
  .nearby_home{
    background: white;
    width: 100%;
    height: 100%;
  }
  .nearby_one{
    border-bottom: 1px solid rgba(0,0,0,0.1);
    padding: 0.18rem 0.08rem;
  }
  .nearby_one_img{
    width: 0.64rem;
    height: 0.64rem;
  }
  .nearby_one_img img{
    width: 100%;
    height: 100%;
  }
  .message{
    width: 2.9rem;
    .message_two{
      padding: 0.15rem 0;
      .rate{
        display: inline-block;
      }
    }
  }

</style>



