<!--兑换红包-->
<template>
  <div class="top" style="position: relative">
    <van-nav-bar title="下载" left-arrow  @click-left="onClickLeft"/>
    <div class="fei" style="position: absolute;top: 0.6rem;left: 0.1rem">
      <input style="width:3.5rem;height: 0.5rem;border-radius: 0.02rem;" type="text " placeholder="请输入兑换码">
    </div>
    <div class="fei" style="position: absolute;top: 1.2rem;left: 0.1rem">
      <input style="width:2rem;height: 0.5rem;border-radius: 0.02rem;" type="text " placeholder="验证码">
      <div style="width:1.45rem;height: 0.5rem;border-radius: 0.02rem;background-color: white;position: absolute;left: 2.1rem;top: 0rem;">
        <img :src="img" alt="" style="margin-top: 0.1rem">
        <p style="position: absolute;left: 0.85rem;top: 0.05rem;">看不清</p>
        <p @click="post_data" style="color: #3b95e9;position: absolute;left: 0.85rem;top: 0.3rem;">换一张</p>
      </div>
    </div>
    <button style="position: absolute;top: 2rem;background-color: #ccc;width: 89%;height: 0.45rem;border: 0;color: white;border-radius: 0.05rem;left: 0.2rem">兑换</button>
  </div>
</template>

<script>
  import Vue from "vue"
    export default {
        name: "envelopes",
      data(){
          return{
            img:""
          }
      },
      // components: {All_head},
      mounted(){
        // this.$refs.register.style.height = window.innerHeight+"px";
        // this.post_data();


          Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
            //后台传来的值存在result的data里面
            console.log(result.data);
            this.img=result.data.code;
          })


      },
      methods:{

        onClickLeft(){
          this.$router.go(-1);
        },
        post_data(){
          Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
            //后台传来的值存在result的data里面
            console.log(result.data);
            this.img=result.data.code;
          })
        }
      },
      // components:{}
    }

</script>

<style scoped>
  .van-nav-bar__title{
    color: white;
    font-weight: 700;
    font-size: 0.2rem;
  }
  .van-nav-bar{
    background-color: rgb(49,144,232);

  }
  :before{
    color: white;
    font-size: 0.2rem;
    font-weight: 700;
  }


</style>



