<!--红包说明-->
<template>
  <div class="hbDescription">
    <div class="head">
      <van-nav-bar  left-arrow  title="红包说明" @click-left="onClickLeft"/>
    </div>
    <!--红包内容部分-->
    <div class="duan">
      <div>
        <div style="margin-top: 0.2rem" v-for="(item,i) in bbb" :key="i">
          <h3 style="font-size: 0.14rem;margin-top: 0rem;line-height: 0.2rem">{{item[1]}}{{item[2]}}</h3>
          <div v-for="(a,i) in item.slice(2)" :key="i">
            <p style="font-size: 0.14rem;margin-top: 0.1rem;color: gray">{{a}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import  Vue from 'vue';
    export default {
        name: "hbDescription",
        methods: {
        onClickLeft() {
              this.$router.go(-1)

        }
      },

      data() {
        return {
          yue:'',
          arr:[],
          arry:[],
        }
      },
      created(){
          Vue.axios.get('https://elm.cangdu.org/v3/profile/explain').then((response) =>{
            // console.log(response.data.hongbaoContent);
            this.yue= response.data.hongbaoContent;
          })
      },
      computed:{

        bbb(){
          this.arr = this.yue.split('###');
          this.arr.map((a)=>{
            console.log(a)
            this.arry.push(a.split(' '));
            // console.log(a)
            // console.log(this.arry);
          });
          // console.log(bbb)
          this.arry.splice(0,1);
          console.log(this.arry);
          this.arry.map((b)=>{
            for(var i=0; i<b.length;i++){
              if(b[i]=="*"){
                b.splice(i,1,'')
              }
              if(b[i]=='在个人中心'){
                b.splice(i+1,3,'我的红包>')
              }
            }
          })
          // console.log(this.arry)
          return this.arry;
        }
      }
    }
</script>

<style scoped>
  .van-nav-bar{
    background-color: rgb(49,144,232);
    height: 0.4rem;
    /*line-height: 0.9rem;*/
  }
  .van-nav-bar__title {

    color:white;
    font-weight: 700;
    font-size: 0.2rem;
  }
  :before {
    color: white;
    font-size: 0.28rem;
    line-height: 0.4rem;
  }
  .hbDescription{
    background-color: #fff;
  }

  .duan{
    padding:0.1rem;
  }
  .duan h3{
    margin:0.1rem 0;
    line-height: 0.05rem;
    font-size: 0.17rem;
  }
  .duan p{
    /*line-height:0.2rem;*/
    margin: 0.1rem 0;
    color: #666666;
    font-size: 0.2rem;
  }
</style>



