import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);

import pos from "../components/position_Sir"
import sign_in from "../components/sign_in"
import register from "../components/register"
import city from "../components/city_position"
import reset from "../components/reset"
import spHome from "../components/shopHome/spHome"
import home1 from "../components/shopHome/spHome1"
import fenlie from '../components/shopHome/shop_waimai/fenlie'
import dianpu from '../components/shopHome/shop_waimai/shop_dianpu'
import pingjia from '../components/shopHome/shop_waimai/pingjia'
import home2 from "../components/shopHome/spHome2"
import home3 from "../components/shopHome/spHome3"
import home4 from "../components/shopHome/spHome4"
//服务中心
import fuwu_home from "../components/shopHome/home_my/fuwuzhongxin/fuwu_home"
import fuwu_once from "../components/shopHome/home_my/fuwuzhongxin/fuwu_once"
//下载app
import xiazai from "../components/shopHome/home_my/xiazai/xiazai"

// 强强的
// import peop from "../components/people"


import down from "../components/download"
import mi from "../components/data_mi"
import int from "../components/integral"
import us from "../components/user_name"
import mem from "../components/member"
import dis from "../components/discount"
import pay from "../components/payment"
import pur from "../components/purchase"
import ex from "../components/exchange"
import ba from "../components/balance"
import miin from "../components/miintegral"
import inte from "../components/integral_description"
import sta from "../components/statement"
import pass from "../components/passHongbao"
import hong from "../components/HongBao"
import bus from "../components/business_description"
import hbd from "../components/hbDescription"
import dai from "../components/daimoneyspaek"
import env from "../components/envelopes"


export default new Router({
  routes: [
    {path:"",redirect:"/position_Sir"},
    {path:"/position_Sir",component:pos},
    {path:"/sign_in",component:sign_in},
    {path:"/register",component:register},
    {path:"/city",component:city},
    {path:"/reset",component:reset},
    {path:"/spHome",component:spHome,children:[
        {path:'',redirect: 'home1'},
        {path:'home1',component:home1},
        {path:'home2',component:home2},
        {path:'home3',component:home3},
        {path:'home4',component:home4},
      ]},
    {path:'/fuwu_home',component:fuwu_home},
    {path:'/xiazai',component:xiazai},
    {path:'/fenlei',component:fenlie},
    {path:'/dianpu',component:dianpu},
    {path:'/pingjia',component:pingjia},
    {path:'/',component:pingjia},
    {path:'/fuwu_once',component:fuwu_once},

    //强强的
    // {path:"/people",component:peop},


    {path:"/download",component:down},
    {path:"/data_mi",component:mi},
    {path:"/integral",component:int},
    {path:"/user_name",component:us},
    {path:"/member",component:mem},
    {path:"/discount",component:dis},
    {path:"/payment",component:pay},
    {path:"/purchase",component:pur},
    {path:"/exchange",component:ex},
    {path:"/balance",component:ba},
    {path:"/miintegral",component:miin},
    {path:"/integral_description",component:inte},
    {path:"/statement",component:sta},
    {path:"/passHongbao",component:pass},
    {path:"/HongBao",component:hong},
    {path:"/business_description",component:bus},
    {path:"/hbDescription",component:hbd},
    {path:"/daimoneyspaek",component:dai},
    {path:"/envelopes",component:env}
  ]
})



