<!--历史红包-->
<template>

  <div class="Voucher">
    <van-nav-bar  left-arrow  title="历史红包" @click-left="onClickLeft"/>
    <van-tabs @click="onClick">
      <van-tab title="红包">
        <div class="discountsBox">
          <div class="discountsMD clearfix">
            <span class="pull-left" style="position: absolute;top: 0.6rem;left: 0.2rem;font-size: 0.12rem">有 <b>{{nums}}</b>个红包即将到期</span>
              <span class="pull-right">
          <img style="width: 0.13rem;position: absolute;top: 0.63rem;left: 2.7rem;display: inline-block" src="../assets/img/description.png"/>
            <span :to="{path:'/hbDescription'}"  style="font-size: 0.12rem;position: absolute;top: 0.6rem;display: inline-block;left: 2.9rem" @click="xin">红包说明</span>
        </span>
          </div>
          <div class="packet clearfix" v-for="(pack, index) in disArr" :key="index">
            <div class="packet_left pull-left">
              <span style="font-size: 0.15rem;font-weight: 700;margin-left: -0.2rem;">￥</span><span style="font-size: 0.35rem">{{pack.amount}}<span style="font-size: 0.2rem;font-weight: 700">.0</span></span>
              <p style="display: inline-block;margin-left: -0.2rem;">{{pack.description_map.sum_condition}}</p>
            </div>
            <div class="packet_right pull-left clearfix">
              <div class="packet_right_left pull-left" style="width: 100%;margin-left: 0.3rem;margin-top: 0.1rem">
                <h4 >{{pack.name}}</h4>
                <p style="color: #aaa;">{{pack.description_map.validity_periods}}</p>
                <p style="color: #aaa;">{{pack.description_map.phone}}</p>
              </div>
            </div>
            <div class="pull-right">
              <div style="color:#ff5340;font-size: 0.2rem;position: absolute;left: 2.8rem;">{{pack.description_map.validity_delta}}</div>
            </div>
          </div>
          <!--<p class="leip">限品类:快餐便当、特色菜系、小吃夜宵、甜品饮品、异国料理</p>-->
          <div class="text-center historia">
            <router-link :to="{path:'/passhongbao'}">
              查看历史红包
              <img src="../assets/img/右箭头灰色.png" class="link_img"/>
            </router-link>
          </div>
        </div>
        <footer class="header_link header_link_a text-center">
          <router-link :to="{path:'/envelopes'}" style="border-right: 1px solid gray;">兑换红包</router-link>
          <router-link :to="{path:'/business_description'}">推荐有奖</router-link>
        </footer>
      </van-tab>
      <van-tab title="商家代金劵">
       <div style="position: relative">
         <img style="position: absolute;right: 1rem;width: 0.13rem;top: 0.1rem;" src="../assets/img/description.png">
         <span style="position: absolute;right: 0.1rem;font-size: 0.12rem ;top: 0.1rem; color: #3190e8;"@click="haha">商家代金券说明</span>
         <img style="position: absolute;left: 1.1rem;top: 1.5rem;width: 1.5rem;" src="data:image/png;base64,"/>
         <p style="position: absolute;top: 2.5rem;left: 1.3rem;">无法使用代金券</p>
         <p style="position: absolute;top: 2.8rem;left: 1.15rem;font-size: 0.12rem;color: gray">非客户端或客户端版本过低</p>
         <button @click="fei" style="background-color: #56d176;position: absolute;border: 0;color: white;width: 1.4rem;text-align: center;height: 0.35rem;border-radius: 0.05rem;top: 3rem;left: 1.15rem">
           下载或升级客户端
         </button>
       </div>
      </van-tab>
    </van-tabs>


  </div>
</template>

<script>
  import Vue from 'vue'
  export default {
    name: "HongBao",
    data(){
      return {
        nums:'',
        disArr:[],
        flavor:''
      }
    },
    mounted(){
      Vue.axios.get('https://elm.cangdu.org/promotion/v2/users/1/hongbaos?limit=20&offset=0').then((res)=>{
        console.log(res);
        this.nums = res.data.length;
        this.disArr = res.data;
        // console.log(this.disArr);
      }).catch((error)=>{
        console.log(error);
      })
    },
    methods:{
      onClick(name, title){
        this.$toast(title);
      },
      onClickLeft(){
        this.$router.go(-1)
      },
      fei(){
      this.$router.push({path:"/download"})
  },
      xin(){
        this.$router.push({path:"/hbDescription"})
      },
  haha(){
this.$router.push({path:"/daimoneyspaek"})
  },
    }




  }
</script>

<style scoped>
  .van-nav-bar{
    background-color: rgb(49,144,232);

  }
  .van-nav-bar__title {

    color:white;
    font-weight: 700;
    font-size: 0.18rem;
  }
  :before{
    color: white;
    font-size: 0.25rem;

  }
  .header_link_a{

  }
  .header_link_a a{
    font-size: 0.2rem;
    color: #aaa;
  }
  .link_img{
    width: 0.4rem;
  }
  .packet_right_left h4:nth-child(1){
    margin-bottom:0 ;
  }
  .packet_right_left p:nth-child(2) ,
  .packet_right_left p:nth-child(3){
    font-size: 0.1rem;
    margin-bottom: 0;
  }
  .packet_right{
    margin-top: -0.2rem;
    padding-left: 0.5rem;
    line-height: 0.12rem;
  }
  .packet_left{
    padding-right: 0.1rem;
    border-right: 1px dashed #aaaaaa;
    height: 100%;
  }
  .packet_left p:nth-child(3){
    /*margin-top: 0.5rem;*/
    font-size: 0.1rem;
    color: #aaaaaa;
    display:block !important;
    margin-top: 0.1rem;
  }
  .packet_left span:nth-child(1){
    font-size: 0.08rem;
  }
  .packet_left span{
    color: #ff5340;
    font-size: 0.2rem;
  }
  .packet{
    width: 3.5rem;
    height: 1rem;
    background: url("../assets/img/hongbao.png")repeat-x;
    background-size: 0.15rem;
    background-color: white;
    margin-top: 0rem;

    padding: 0.5rem;
    border-radius: 0.3rem;
    line-height: 0.1rem;
  }
  .discountsBox{
    padding: 0.5rem 0.1rem;

  }
  .discountsMD b{
    color: red;
  }
  .discountsMD img{
    width: 0.6rem;
  }
  .discountsMD{
    font-size: 0.5rem;
  }
  .discountsMD span{
    line-height: 0.2rem;
    font-size: 0.1rem;
  }
  .header_link{
    width: 100%;
    background: white;
    margin-top:1rem;
    padding: 0.1rem 0;

  }
  .header_link a{
    width: 0.1rem;
    height: 0.1rem;
    line-height: 0.2rem;
    /*display: inline-block;*/
    color: black;
  }
  .leip{
    color: #999999;
    margin-top: 0.5rem;
  }
  .historia a{
    color: #999999;
    font-size:0.1rem;
    margin-top:2rem;
  }
  .historia{
    margin-top: 0.2rem;
  }
  .historia img{
    width: 0.1rem;
    vertical-align: middle;
  }
</style>



