<!--我的积分-->
<template>
  <div class="top" style="position: relative">
    <van-nav-bar
      title="我的积分"
      left-arrow @click-left="onClickLeft"/>
    <div style="width: 96%;height: 1.7rem;position: absolute;top: 0.6rem;left:0.07rem;background-color: white;z-index: 2;border-radius: 0.03rem">
      <span style="font-size: 0.13rem;position: absolute;top: 0.1rem;left: 0.1rem;">当前积分</span><span><img src="data:image/png;base64," style="position: absolute;left: 2.8rem;top: 0.1rem;width: 0.14rem"/></span><span style="color: cornflowerblue ;position: absolute;right: 0.1rem;font-size: 0.13rem;top: 0.1rem;" @click="heihei">积分说明</span>
    </div>
    <span style="position: absolute;top: 1rem;z-index: 5;left: 0.2rem;font-size: 0.4rem">0</span><span style="position: absolute;left: 0.5rem;top: 1.15rem;z-index: 5;">分</span>
    <button @click="yes" style="width: 90%;height: 0.5rem;border: 0;color: white;border-radius: 0.05rem;position: absolute;top: 1.7rem;z-index: 5;left: 0.18rem;background-color: rgb(254,109,71);">积分兑换商品</button>
    <p style="font-size: 0.14rem;color: gray;position: absolute;top: 2.6rem;left: 0.15rem">最近30天积分记录</p>
    <img src="data:image/png;base64," style="width:2rem;height:1.2rem;position: absolute;left: 0.9rem;top: 3rem;"/>
    <p style="position: absolute;left: 1.16rem;top: 4.3rem;font-size: 0.17rem;color: gray">最近30天积分记录</p>
    <p style="color: gray;font-size: 0.11rem;position: absolute;top: 4.6rem;z-index: 5;left: 1.2rem">快去下单赚取大量积分吧</p>
    <div class="d1" v-if="show">
      <img class="chu" src="../assets/img/222.png"/>
      <h4>快去下单赚取大量积分吧</h4>
      <button class="butt" @click="no">确定</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: "miintegral",
    data(){
      return{
        show:false,
      }
    },
    methods:{
      onClickLeft(){
        this.$router.go(-1);
      },
      yes(){
        this.show=true;
      },
      no(){
        this.show=false;
      },
      heihei(){
        this.$router.push({path:"/integral_description"})
      }
    }
  }






</script>

<style scoped>
  .van-nav-bar{
    background-color: rgb(49,144,232);
    height: 2.38rem;
    /*line-height: 0.9rem;*/
  }
  .van-nav-bar__title {

    color:white;
    font-weight: 700;
    font-size: 0.2rem;
  }
  :before{
    color: white;
    font-size: 0.28rem;
    line-height: 4.2rem;

  }
  .d1{
    position: absolute;
    top: 2.3rem;
    left: 0.35rem;
    z-index: 2;
    background-color: white;
    width: 80%;
    height: 1.5rem;
    border-radius: 0.1rem;
    text-align: center;
  }
  .butt{
    width: 100%;
    height: 0.4rem;
    border-radius: 0px 0px 0.1rem 0.1rem;
    background-color: rgb(76,217,100);
    margin-bottom: 0px;
    border: 0px;
    font-size: 0.18rem;
    font-weight: 700;
    color: white;
    margin-top: 0.19rem;
  }
</style>




