<template>
    <div class="shop_dianpu" ref="register">
      <div class="top">
        <div class="bg_img">
          <img :src="(!!this.$route.query.img)?'http://elm.cangdu.org/img/'+this.$route.query.img:''" alt="">
        </div>
        <div class="text">
          <img :src="(!!this.$route.query.img)?'http://elm.cangdu.org/img/'+this.$route.query.img:''" alt="">
          <div class="fanhui">
            <span @click="btn_fanhui" class="qyh fanhui_span2">&#xe64c;</span>
            <span class="fanhui_span1">{{this.$route.query.name}}</span>
          </div>
          <div class="ersan">
            <div class="er"><span>商家配送 /</span><span>分钟送达 /</span><span>配送费¥5</span>
            </div>
            <span class="qyh fanhui_ding">&#xe625;</span>
            <div class="san">
              公告 : 1
              <!--<span>商家配送</span><span>商家配送</span><span>商家配送</span>-->
            </div>
          </div>
          <div class="si">
            <div class="si_1"><span class="jian">减</span><span>满30减5,  满60减8(APP专享)</span></div>
            <div class="si_2"><span>1个活动</span><span class="qyh" style="font-size: 0.2rem">&#xe625;</span></div>
          </div>
        </div>

      </div>

      <div class="opt clearfix">
        <div class="pull-left" @click="btn_change('0')"><span :class="{bor_change:(bor_change==='0')}">商品</span></div>
        <div class="pull-left" @click="btn_change('1')"><span :class="{bor_change:(bor_change==='1')}">评价</span></div>
      </div>

      <div class="bottom clearfix" v-if="bor_change==='0'">
        <div class="left">
          <div class="l">
            <div class="once" :class="{btn_color:change===k}" v-for="(once,k) in allArr" :key="k" @click="btn_name(k)">
              {{once.name}}
              <span>{{num(once.restaurant_id,once.id)}}</span>
            </div>
          </div>
        </div>
        <div class="right">
          <transition name="anm">
            <div class="r" ref="ul" @touchmove="dong">
              <div class="data_arr" v-for="(once,k) in allArr" :key="k" ref="li">
                <div class="cao clearfix">
                  <span class="rexiao pull-left">{{once.name}}</span><span class="shide pull-left">{{once.description}}</span><span class="dian pull-right">...</span>
                </div>
                <div class="shuzi">
                  <div class="one" v-for="(one,k2) in once.foods" :key="k2">
                    <div class="two clearfix">
                      <div class="img pull-left">
                        <img :src="'http://elm.cangdu.org/img/'+one.image_path" alt="">
                      </div>
                      <div class="text pull-left">
                        <p>{{one.name}}</p>
                        <p>{{one.description}}</p>
                        <div><span>月销{{one.month_sales}}份</span><span>好评率{{one.satisfy_rate}}%</span></div>
                        <p>{{one.activity?one.activity.image_text:''}}</p>
                        <div class="clearfix">
                          <span>¥</span><span>{{one.specfoods[0].price}}</span>
                          <p class="pull-right" v-if="!!!one.specfoods[0].specs.length>0">
                            <span class="pull-right jia" @click="add(one,once.restaurant_id)">+</span>
                            <span class="pull-right num" v-if="$store.state.a.shopCartObj[once.restaurant_id][one.item_id]">{{$store.state.a.shopCartObj[once.restaurant_id][one.item_id].count}}</span>
                            <span class="pull-right jian" v-if="$store.state.a.shopCartObj[once.restaurant_id][one.item_id]">-</span>
                          </p>
                          <div class="pull-right" v-else>
                            <span @click="btn_guige(one)">选规格</span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div style="clear:both"></div>
                </div>
              </div>
            </div>
          </transition>
        </div>
      </div>
      <pingjia v-else></pingjia>

      <!--规格界面-->
      <div class="guige" v-if="guige" @click="beijing"></div>
      <div class="all" v-if="guige">
        <div class="one">{{guigeData.name}}</div>
        <p>规格</p>
        <div>{{guigeData.specfoods[0].specs_name}}</div><div>{{guigeData.specfoods[1].specs_name}}</div>
        <span>¥</span><span>{{guigeData.specfoods[guige_num].price}}</span>
        <span>加入购物车</span>
      </div>
    </div>
</template>

<script>
  import Pingjia from "./pingjia";
  export default {
    name: "shop_dianpu",
    components: {Pingjia},
    data(){
      return{
        bor_change:'0',
        allArr:[],//所有的数据
        change:0,//右边选择,
        lis:[],//记录所有的li的高度
        guige:false,
        //规格界面
        guigeData:null,
        guige_num:0,
      }
    },
    methods:{
      btn_fanhui(){
        this.$router.go(-1);
      },
      btn_change(v){
        this.bor_change=v;
      },
      btn_name(k){
        this.change=k;
        this.$refs.ul.scrollTop = this.$refs.li[k].offsetTop;
      },
      dong(){
        if (this.lis.length === 0) {
          for (let i=0;i<this.allArr.length;i++) {
            this.lis.push(this.$refs.li[i].offsetHeight)
          }
        }
        let a=0;
        if (this.$refs.ul.scrollTop < this.lis[0]) {
          this.change=0;
        }else {
          for (let i = 0; i < this.lis.length; i++) {
            a+=this.lis[i];
            if (this.$refs.ul.scrollTop>a){
              this.change=i+1;
            }
          }
        }

      },
      add(v,k2){
        let category_id=v.category_id;//热门分类
        let item_id=v.item_id;//商品id
        let name=v.specfoods[0].name;//商品名字
        let price=v.specfoods[0].price;//价格
        let sku_id=v.specfoods[0].sku_id;//选择种类id
        let name1=v.specfoods[0].specs_name;//种类名字
        let k = k2;//店铺编号
        console.log(k,category_id,item_id,name,price,sku_id,name1);
        console.log(v);
        this.$store.commit("addShop");
      },
      btn_guige(v){
        console.log(v);
        this.guige=true;
        this.guigeData=v;
      },
      beijing(){
        this.guige=false;
      },
      num(k,k2){
        //k店铺id,k2热门分类
        console.log(k,k2);
        let p=0;
        if (!!this.$store.state.a.shopCartObj[k]) {
          let a= this.$store.state.a.shopCartObj[k];
          for (let v in a) {
            console.log(a[v].restaurant_id);
            if (a[v].restaurant_id===k2) {
              p+=a[v].count;
            }
          }
        }
        return p;
      }
    },
    computed:{

    },
    created(){
      this.axios.get('https://elm.cangdu.org/shopping/v2/menu',{
        params:{
          restaurant_id:this.$route.query.id
        }
      }).then(result=>{
        this.allArr=result.data;
        console.log(result.data);
      });
    },
    mounted(){
      this.$refs.register.style.minHeight = window.innerHeight+"px";
    },
  }
</script>

<style scoped lang="less">
  .shop_dianpu{
    position: relative;
    height: 100%;
    width: 100%;
    background-color: #f5f5f5;
    .top{
      box-sizing: border-box;
      width: 100%;
      height: 1.25rem;
      position: relative;
      color: white;
      .bg_img{
        width: 100%;
        height: 100%;
        overflow: hidden;
        img{
          width: 100%;
          -webkit-filter: blur(10px);
          -moz-filter: blur(10px);
          -o-filter: blur(10px);
          -ms-filter: blur(10px);
          filter: blur(10px);
        }
      }
      .text{
        position: absolute;
        top:0.08rem;
        left:0.08rem;
        right: 0;
        bottom: 0;
        background-color: transparent;
        img{
          width: 0.7rem;
        }
        .fanhui{
          position: absolute;
          top: 0;
          left: 0;
          .fanhui_span1{
            margin-left: 0.57rem;
          }
          .fanhui_span2{
            font-size: 0.2rem;
            margin-left: -0.07rem;
          }
        }
        .ersan{
          display: inline-block;
          .fanhui_ding{
            font-size: 0.3rem;
            position: fixed;
            top:  0.5rem;
            left: 3.4rem;
          }
          .er{
            font-size: 0.05rem;
            padding-top: 0.2rem;
            display: inline-block;
          }
          .san{
            font-size: 0.05rem;
            margin-top: 0.1rem;
          }
        }
        .si{
          display: inline-block;
          font-size: 0.05rem;
          width: 100%;
          margin-top: 0.18rem;
          .si_1{
            float: left;
            .jian{
              display: inline-block;
              width: 0.15rem;
              height: 0.15rem;
              line-height: 0.15rem;
              border-radius: 0.03rem;
              background: rgba(240,115,115);
              margin-right: 0.05rem;
            }
          }
          .si_2{
              float: right;
            margin-right: 0.15rem;
            margin-top: -0.05rem;
          }
        }
      }
    }
    .opt{
      height: 0.6rem;
      box-sizing: border-box;
      padding: 0.2rem 0;
      border-bottom: 1px rgba(0,0,0,0.1) solid;
      div{
        width: 50%;
        text-align: center;
      }
    }
    .bottom{
      width: 100%;
      position: absolute;
      top: 1.85rem;
      bottom: 0;
      .left{
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        width: 25%;
        overflow: hidden;
        height: 100%;
        .l{
          width: 110%;
          height: 100%;
          overflow-y: scroll;
          overflow-x: hidden;
          .once{
            height: 0.7rem;
            box-sizing: border-box;
            padding: 0.25rem 0 0 0.05rem;
            border-bottom: 1px rgba(0,0,0,0.1) solid;
          }
        }
      }
      .right{
        width: 75%;
        position: absolute;
        top: 0;
        left: 25%;
        bottom: 0;
        overflow: hidden;
        transition: all 5s linear;
        .r{
          margin-right: -10%;
          width: 110%;
          overflow-y: scroll;
          overflow-x: hidden;
          height: 100%;
          transition: all 5s linear;
          .data_arr{
            width: 100%;
            .cao{
              background-color: rgba(0,0,0,0.05);
              width: 100%;
              box-sizing: border-box;
              height: 0.58rem;
              line-height: 0.58rem;
              .rexiao{
                margin-left: 0.05rem;
                margin-right: 0.05rem;
                color: rgba(0,0,0,0.5);
                font-weight: 900;
              }
              .shide{
                font-size: 0.01rem;
                color: #666;
              }
              .dian{
                color: #666;
                padding-right: 10%;
              }
            }
            .shuzi{
              .one{
                padding-right: 10%;
                border-bottom: 1px solid rgba(0,0,0,0.1);
                background-color: white;
                .two{
                  padding: 0.1rem;
                  width: 100%;
                  .img{
                    width: 15%;
                    height: 100%;
                    img{
                      width: 100%;
                    }
                  }
                  .text{
                    width: 85%;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .bor_change{
    color: #3190e8;
    border-bottom: 3px #3190e8 solid;
  }
  .btn_color{
    border-left: 2px solid #3190e8;
    background-color: white;
  }

  .guige{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0,0.2);
    z-index: 49;

  }
  .all{
    position: fixed;
    top: 33%;
    bottom: 33%;
    left: 12%;
    right: 12%;
    z-index: 50;
    background-color: salmon;
    border-radius: 4px;
  }
</style>



