<template>
  <div class="fuwu_once">
    <all_head>
      <template v-slot:one><span @click="btn"><</span></template>
      <template v-slot:two>{{$route.query.name}}</template>
    </all_head>
    <p class="a">{{$route.query.text}}</p>
  </div>
</template>

<script>
  import All_head from "../../../all_head";
  export default {
    name: "fuwu_once",
    components: {All_head},
    data(){
      return {
        dataArr:[]
      }
    },
    created(){
      // console.log(this.$route.query.text);
      let a=this.$route.query.text.split(' ');
      // console.log(a);
    },
    methods:{
      btn(){
        this.$router.go(-1)
      }
    }
  }
</script>

<style scoped>
  .fuwu_once{

    font-size: 0.2rem;
  }
  .a{
    padding: 0.1rem;
    line-height: 0.3rem;
    padding-top: 0.48rem;
  }
</style>



