<template>
    <div class="fenlie">
      <all_head>
        <template v-slot:one><p @click="aa" class="qyh fdj">&#xe65d;</p></template>
        <template v-slot:two>{{name}}</template>
        <template v-slot:three></template>
      </all_head>

      <div class="top">
        <div class="d1" @click="str1_chang('1')">{{fl}}<span class="qyh" v-if="str1==='1'">&#xe605;</span>
          <span class="qyh" v-else>&#xe611;</span>
          </div>
        <div class="d2" @click="str1_chang('2')">{{px}}<span class="qyh"  v-if="str1==='2'">&#xe605;</span>
          <span class="qyh" v-else>&#xe611;</span>
          </div>
        <div class="d3" @click="str1_chang('3')">{{sx}}<span class="qyh"  v-if="str1==='3'">&#xe605;</span>
          <span class="qyh" v-else>&#xe611;</span>
          </div>
      </div>

      <div class="fl clearfix" v-if="str1==='1'">
        <div style="overflow: hidden;width: 50%;height: 100%;" class="pull-left">
          <div class="left">
            <div class="all_shop qyh" v-for="(once,k) in dataArr" :key="k" @click="btn_all(k)">
              <span class="span1">&#xe658;{{once.name}}</span>
              <span class="span2">
                <span class="span3">{{once.count}}</span>
              &#xe625;
              </span>
              <div style="clear: both "></div>
            </div>
          </div>
        </div>
        <div style="overflow: hidden;width: 50%;height: 100%;" class="pull-left">
          <div class="right">
            <div class="fenlie2 clearfix" v-for="(once,k) in dataArr_right" :key="k" @click="btn_fenlei(once.id)">
              <span style="float: left">{{once.name}}</span>
              <span style="float: right ">{{once.count}}</span>
            </div>
          </div>
        </div>
      </div>

      <div class="px qyh" v-else-if="str1==='2'">
        <ul>
          <li @click="btn_px(4)">
            <span class="span_left" style="color: dodgerblue;">&#xe648;</span>
            <span class="span_right">智能排序</span>
          </li>
          <li @click="btn_px(5)">
            <span class="span_left" style="color: dodgerblue;">&#xe61c;</span>
            <span class="span_right">距离最近</span>
          </li>
          <li @click="btn_px(6)">
            <span class="span_left" style="color: red;">&#xe607;  </span>
            <span class="span_right">销量最高</span>
          </li>
          <li @click="btn_px(1)">
            <span class="span_left" style="color: yellow;">&#xe60a;</span>
            <span class="span_right" id="span_right">起送价最低</span>
          </li>
          <li @click="btn_px(2)">
            <span class="span_left" style="color: greenyellow">&#xe61e;</span>
            <span class="span_right">配送速度最快</span>
          </li>
          <li @click="btn_px(3)">
            <span class="span_left" style="color: yellow">&#xe643;</span>
            <span class="span_right">评分最高</span>
          </li>
        </ul>
      </div>

      <div class="sx qyh" v-else-if="str1==='3'">
        <p class="sx_p">配送方式</p>
        <button @click="btn_sx(0)"><span style="color: dodgerblue; margin-left: -0.2rem;font-size: 0.2rem" v-if="!sxArr[0]">&#xe609;</span><span class="qyh" v-else>&#xe60b;</span><span>蜂鸟转送</span></button>
        <br>
        <p class="sx_p">商家属性(可以多选)</p>
        <button @click="btn_sx(1)"><span class="bs" style="border: 1px solid rgb(63, 189, 230);color: rgb(63, 189, 230)" v-if="!sxArr[1]">品</span><span class="qyh" v-else>&#xe60b;</span><span class="bs1">品牌商家</span></button>
        <button @click="btn_sx(2)"><span class="bs" style="border: 1px solid rgb(153, 153, 153);color: rgb(153, 153, 153)" v-if="!sxArr[2]">保</span><span class="qyh" v-else>&#xe60b;</span><span class="bs1">外卖保</span></button>
        <button @click="btn_sx(3)"><span class="bs" style="border: 1px solid rgb(87, 169, 255);color: rgb(87, 169, 255)" v-if="!sxArr[3]">准</span><span class="qyh" v-else>&#xe60b;</span><span class="bs1">准时达</span></button>
        <br>
        <button @click="btn_sx(4)"><span class="bs" style="border: 1px solid rgb(232, 132, 45);color: rgb(232, 132, 45)" v-if="!sxArr[4]">新</span><span class="qyh" v-else>&#xe60b;</span><span class="bs1">新店</span></button>
        <button @click="btn_sx(5)"><span class="bs" style="border: 1px solid rgb(255, 78, 0);color: rgb(255, 78, 0)" v-if="!sxArr[5]">付</span><span class="qyh" v-else>&#xe60b;</span><span class="bs1">在线支付</span></button>
        <button @click="btn_sx(6)"><span class="bs" style="border: 1px solid rgb(153, 153, 153);color: rgb(153, 153, 153)" v-if="!sxArr[6]">票</span><span class="qyh" v-else>&#xe60b;</span><span class="bs1">开发票</span></button>
        <br>
        <div class="wei">
          <button class="wei_left" @click="btn_qingkong">清空</button>
          <button class="wei_right" @click="btn_sure">确定 <span v-if="num3>0">({{num3}})</span></button>
          <div style="clear: both"></div>
        </div>
      </div>

      <div class="nearby_home">
        <div class="nearby_one" v-for="(once,key) in shoperArr" :key="key" @click="btn_jump(once.name,once.id,once.image_path)">
          <div class="nearby_one_img pull-left">
            <img :src="(!!once.image_path)?'http://elm.cangdu.org/img/'+once.image_path:''" alt="图片加载失败">
          </div>
          <div class="message pull-right">
            <div class="message_one">
              <span class="message_one_p1">品牌</span>
              <span class="message_one_p2">{{(once.name)?once.name:''}}</span>
              <span class="pull-right message_one_p3">保</span>
              <span class="pull-right message_one_p4">准</span>
              <span class="pull-right message_one_p5">票</span>
            </div>
            <div class="message_two clearfix">
              <div class="rate">
                <van-rate size="0.1rem" gutter="0" v-model="once.rating" readonly allow-half void-icon="star" void-color="#eee"/>
              </div>
              <span>{{once.rating}}</span>
              <span>月销{{once.recent_order_num}}单</span>
              <span class="pull-right">{{(!!once.supports[1])?(!!once.supports[1].name?once.supports[1].name:''):''}}</span>
              <span class="pull-right">{{(!!once.delivery_mode)?(!!once.delivery_mode.text?once.supports[1].name:''):''}}</span>
            </div>
            <div class="message_three clearfix">
              <p class="pull-left">¥{{once.float_minimum_order_amount}}起送<span>/</span>配送费约¥{{(!!once.float_delivery_fee)?once.float_delivery_fee:0}}</p>
              <p class="pull-right">{{once.distance}}<span>/</span>{{once.order_lead_time}}</p>
            </div>
          </div>
          <div class="clearfix"></div>
        </div>
      </div>

      <div :class="{yinyng:(str1!=='')}" @click="str1=''"></div>
    </div>
</template>

<script>
  import All_head from "../../all_head";
  import Vue from 'vue';
  export default {
    name: "fenlie",
    components: {All_head},
    data(){
      return {
        geohash:[],
        dataArr:[],//全部的数据
        dataArr_right:[],//右边的分类
        name:'',
        //选项
        fl:"分类",
        px:"排序",
        sx:'筛选',
        //筛选显示第几块
        str1:'',
        shoperArr:[],//初始显示的内容
        sxArr:[false,false,false,false,false,false,false],//判断是否选中
      }
    },
    methods:{
      aa(){
        this.$router.go(-1);
      },
      btn_all(k){
        this.dataArr_right=this.dataArr[k].sub_categories;
        // this.dataArr_right.splice(0,1);
      },
      str1_chang(v){
        this.str1=(this.str1===v?'':v);
      },
      //获取分类
      btn_fenlei(v){
        console.log(v);
        this.str1='';
        localStorage.restaurant_category_ids=v;
        this.axios.get("https://elm.cangdu.org/shopping/restaurants",{
          params:{
            latitude:this.geohash[0],
            longitude:this.geohash[1],
            'restaurant_category_ids[]':v,
            order_by:(!!localStorage.order_by)?localStorage.order_by:'',
          }
        }).then(result=>{
          console.log(result.data);
          this.shoperArr=result.data;
        })
      },
      btn_px(k){
        this.str1='';
        localStorage.order_by=k;
        this.axios.get("https://elm.cangdu.org/shopping/restaurants",{
          params:{
            latitude:this.geohash[0],
            longitude:this.geohash[1],
            'restaurant_category_ids[]':(!!localStorage.restaurant_category_ids)?localStorage.restaurant_category_ids:'',
            order_by:k,
          }
        }).then(result=>{
          this.shoperArr=result.data;
        })
      },
      btn_sx(k){
        Vue.set(this.sxArr,k,!this.sxArr[k]);
      },
      btn_qingkong(){
        this.sxArr=[false,false,false,false,false,false,false];
      },
      btn_sure(){
        let sxArr_get=[this.sxArr[1]?8:'',this.sxArr[2]?7:'',this.sxArr[3]?9:'',this.sxArr[4]?5:'',this.sxArr[5]?3:'',this.sxArr[6]?4:''];//传值的时候数组的数据
        this.axios.get("https://elm.cangdu.org/shopping/restaurants",{
          params:{
            latitude:this.geohash[0],
            longitude:this.geohash[1],
            'restaurant_category_ids[]':(!!localStorage.restaurant_category_ids)?localStorage.restaurant_category_ids:'',
            order_by:(!!localStorage.order_by)?localStorage.order_by:'',
            'delivery_mode[]':this.sxArr[0]?1:'',
            'support_ids':sxArr_get,
          }
        }).then(result=>{
          this.shoperArr=result.data;
        });
        this.str1='';
      },
      btn_jump(name,id,img){
        this.$router.push({path:'/dianpu',query:{name:name,id:id,img:img}});
      }
    },
    computed:{
      //配送多选数量
      num3(){
        let a=0;
        this.sxArr.forEach(v=>{
          if (v) {
            a++;
          }
        });
        return a;
      },
    },

    created(){
      this.geohash=localStorage.qyh_geohash.split(",");
      // let id=this.$route.query.id;
      this.name=this.$route.query.name;
      this.axios.get('https://elm.cangdu.org/shopping/v2/restaurant/category',{
        params:{
          latitude:this.geohash[0],
          longitude:this.geohash[1],
        }
      }).then(result=>{
        this.dataArr=result.data;
        this.dataArr_right=result.data.sub_categories;
        this.dataArr_right=result.data[0].sub_categories;
        this.dataArr_right.splice(0,1);
      });
      this.axios.get('https://elm.cangdu.org/shopping/restaurants',{
        params:{
          latitude:this.geohash[0],
          longitude:this.geohash[1],
        }
      }).then(result=>{
        console.log(result.data);
        this.shoperArr=result.data;
      }).catch(error=>{
        console.log(error);
      })
    }
  }
</script>

<style scoped lang="less">
  .fenlie{
    .fdj{
      font-size: 0.3rem;
    }
    .top{
      background-color: white;
      z-index: 101;
      width: 100%;
      position: fixed;
      top: 0.48rem;
      padding: 0.05rem 0;
      display: flex;
      align-items: center;
      justify-content: space-around;
      border-bottom: 1px rgba(0,0,0,0.1) solid;
      div{
        text-align: center;
        display: inline-block;
        flex-grow: 1;
        font-size: 0.1rem;
        padding: 0.05rem 0;
        span{
          font-size: 0.07rem;
          color: #666;
        }
      }
      .d2{
        border-left: 1px rgba(0,0,0,0.1) solid;
        border-right: 1px rgba(0,0,0,0.1) solid;
      }
    }
    .fl{
      z-index: 100;
      position: fixed;
      padding-top: 0.8rem;
      width: 100%;
      height: 4.6rem;
      /*background: gainsboro;*/
      border-bottom: 1px rgba(0,0,0,0.1) solid;
      .left{
        width: 110%;
        height: 100%;
        background-color: #f5f5f5;
        overflow-y: scroll;
        .all_shop{
          width: 100%;
          height: 0.425rem;
          background-color: #f5f5f5;
          line-height: 0.425rem;
          font-size: 0.1rem;
          color: #666;
          padding: 0 0.15rem;
          .span1{
            float: left;
          }
          .span2{
            float: right;
            .span3{
              background-color: #ccc;
              padding: 0.05rem 0.05rem;
              border-radius: 1rem;
            }

          }
        }
      }

      .right{
        width: 110%;
        height: 100%;
        background-color: white;
        padding-left: 0.2rem;
        overflow-y: scroll;
        margin-right: -0.1rem;
        .fenlie2{
          width: 100%;
          height: 0.4rem;
          line-height: 0.4rem ;
          box-sizing: border-box;
          border-bottom: 1px rgba(0,0,0,0.1) solid;
          padding-left: 0.03rem ;
          padding-right: 0.13rem;
          font-size: 0.1rem;
          color: #666;
        }
      }
    }
    .px{
      z-index: 100;
      position: fixed;
      padding-top: 0.8rem;
      width: 100%;
      /*height: 4.6rem;*/
      background: white;
      /*border-bottom: 1px rgba(0,0,0,0.1) solid;*/
      ul{
        width: 100% ;
        li{
          height: 0.55rem  ;
          line-height: 0.55rem;
          margin-left: 0.15rem;
          color: #666;
          /*border-bottom: 1px solid rgba(0,0,0,0.2);*/
          .span_left{
            margin-right: 0.07rem;
            width: 10%;
            font-size: 0.15rem;
          }
          #span_right{
            width: 91.987%;
          }
          .span_right{
            width: 92.4%;
            display: inline-block;
            border-bottom: 1px solid rgba(0,0,0,0.2);
            font-size: 0.1rem;
          }
        }
      }
    }
    .sx{
      z-index: 100;
      position: fixed;
      padding-top: 0.85rem;
      width: 100%;
      height: 2.8rem;
      background: white;
      font-size: 0.1rem;
      .sx_p{
        padding: 0.08rem 0.1rem;
        color: #666;
      }
      button{
        background: white;
        width: 30%  ;
        height: 0.3rem  ;
        line-height: 0.3rem;
        margin: 0.06rem 0.07rem;
        border: .005rem solid #eee;
        border-radius: 0.02rem;
        margin-right: -0.05rem;
      }
      .bs{
        font-size: 0.13rem ;
        border-radius: 0.05rem;
        padding: 0.025rem ;
        margin-left: -0.25rem ;
      }
      .bs1{
        margin-left: 0.05rem;
      }
      .wei{
        background: #f1f1f1;
        margin-top: 0.13rem;
        font-size: 0.18rem;
        .wei_left{
          float: left;
          height: 0.47rem;
          width: 47%;
          border: .025rem solid white;
          border-radius: 0.07rem;
          color: black;
        }
        .wei_right{
          float: right;
          height: 0.47rem;
          width: 47%;
          border: .025rem solid #56d176;
          border-radius: 0.07rem;
          background: #56d176;
          color: white;
          margin-right: 0.05rem;
        }
      }
    }
    .nearby_home{
      padding-top: 0.8rem;
      background: white;
      width: 100%;
      height: 100%;
      .nearby_one{
        font-size: 0.12rem;
        padding: 0.2rem 0.1rem;
        border-bottom: 1px rgba(0,0,0,0.1) solid;
        .one{
          width: 100%;
          height: 2rem;
          background: white;
        }
        .shop_fenlie{
          width: 25%;
          height: 1rem;
          text-align: center;
          display: inline-block;
        }
        .shop_fenlie img{
          margin: 0.1rem;
          width: 50%;
        }
        .shop_fenlie p{
          font-size: 0.14rem;
          font-weight: 400;
        }
        .span1{
          font-size: 0.2rem;
          color: rgba(0,0,0,0.4);
          margin-left: 0.1rem;
        }
        .span2{
          font-size: 0.13rem;
          color: rgba(0,0,0,0.4);
        }
        .nearby{
          margin-top: 0.11rem;
          padding-top: 0.08rem;
        }
        .nearby_home{
          background: white;
          width: 100%;
          height: 100%;
        }
        .nearby_one{
          border-bottom: 1px solid rgba(0,0,0,0.1);
          padding: 0.18rem 0.08rem;
        }
        .nearby_one_img{
          width: 0.64rem;
          height: 0.64rem;
        }
        .nearby_one_img img{
          width: 100%;
          height: 100%;
        }
        .message{
          padding-left: 0.1rem;
          width: 2.9rem;
          .message_two{
            padding: 0.15rem 0;
            .rate{
              display: inline-block;
            }
          }
        }
      }
    }
    .yinyng{
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 50;
      background-color: rgba(0,0,0,0.3);
    }

  }
</style>



