          }
          ul{
            li{
              display: inline-block;
              border-radius: 0.05rem;
              border: 0.01rem rgba(0,0,0,0.1) solid;
              padding: 0.07rem 0.02rem;
              width: 0.5rem;
              font-size: 0.14rem;
              overflow: hidden; white-space: nowrap; text-overflow: ellipsis;
            }
          }
        }
      }
    }
  }
</style>



