<!--修改用户名-->
<template>

  <div class="top">
    <van-nav-bar title="修改用户名" left-arrow  @click-left="onClickLeft"/>
    <span></span>
    <div class="fu">
      <input type="text" placeholder="输入用户名" class="input1"/>
    </div>
    <div class="la">
      <p>用户名只能修改一次(5-24字符之间)</p>
    </div>
<div class="lei">
  <div class="haha">
    <h3>确认修改</h3>
  </div>
</div>
  </div>

</template>

<script>
    export default {
        name: "user_name",
        data(){
          return{

          }
        },
      methods:{
        onClickLeft(){
          this.$router.push({path:"/data_mi"});
        }
      }
    }
</script>

<style scoped>
  #head_top[data-v-cec0c0c0] {
    position: fixed;
    z-index: 100;
  }
  .top{
    font-size: 0rem;
  }
  html{
    background-color: rgb(242,242,242);

  }
  .la{
    background-color: rgb(242,242,242);
  }
  .van-nav-bar__title{
    color: white;
    font-weight: 700;
    font-size: 0.2rem;
  }
  .van-nav-bar{
    background-color: rgb(49,144,232);
  }
  :before{
    color: white;
    font-size: 0.2rem;
    font-weight: 700;
  }
  .input1{
    width: 89%;
    height: 0.4rem;
    border-radius: 0.01rem;
    font-size: 0.15rem;
    background-color: rgb(242,242,242);
    border: 0.01rem solid gainsboro;

  }
  .fu{
    background-color: rgb(242,242,242);
    text-align: center;

    /*border: 1px solid;*/
    width: 100%;
    height: 0.5rem;
  }
  p{
    font-size: 0.1rem;

    margin-left: 0.2rem;
  }
span{
  width: 100%;
  height: 0.1rem;
  display: inline-block;
  background-color: rgb(242,242,242);
}
.haha{
  position: relative;
  width: 96%;
  height: 0.5rem;
  background-color: rgb(49,153,232);
  position: absolute;
  left: -0.115rem;

}
.lei{
  width: 100%;
  text-align: center;
  position: absolute;
  left: 0.2rem;
  top: 1.4rem;

}
h3{
  position: absolute;
  left: 1.4rem;
  line-height: 0.5rem;
  font-size: 0.2rem;
  color: gainsboro;
}
</style>



