<!--积分问题-->
<template>
  <div class="top">
    <van-nav-bar title="" left-arrow  @click-left="onClickLeft"/>
    <p style="font-size: 0.17rem;margin-top: 0.15rem;margin-left: 0.1rem;">Q1: 怎么获得积分？</p>
    <p style="color: gray;font-size: 0.145rem;margin-top: 0.2rem;margin-left: 0.1rem;">在线支付的订单将获得订单积分奖励：</p>
    <p style="color: gray;font-size: 0.145rem;margin-top: 0.1rem;margin-left: 0.1rem;">积分将在用户完成评价后获得。</p>
    <p style="color: gray;font-size: 0.145rem;margin-top: 0.1rem;margin-left: 0.1rem;">可获得积分=订单金额×10（即1元=10点积分）。</p>
    <p style="color: gray;font-size: 0.145rem;margin-top: 0.1rem;margin-left: 0.1rem;">订单金额指实际付款金额，不包含活动优惠金额。</p>
    <p style="color: gray;font-size: 0.145rem;margin-top: 0.1rem;margin-left: 0.1rem;line-height:0.2rem;">每位用户每天最多可以获得2000积分，体验商家的订单和评价不会增加积分。</p>
    <p  style="font-size: 0.17rem;margin-top: 0.15rem;margin-left: 0.1rem;">Q2: 积分用来做什么？</p>
    <p style="color: gray;font-size: 0.145rem;margin-top: 0.2rem;margin-left: 0.1rem;">可以在积分商城兑换各种礼品。</p>
    <p  style="font-size: 0.17rem;margin-top: 0.15rem;margin-left: 0.1rem;">Q3: 礼品兑换很多天了还没有收到，该怎么办？</p>
    <p style="color: gray;font-size: 0.145rem;margin-top: 0.2rem;margin-left: 0.1rem;line-height:0.2rem;">礼品从兑换日起，3个工作日（周末不算）内处理发货，发货后，通常会在3个工作日左右送达。  </p>
    <p  style="font-size: 0.17rem;margin-top: 0.15rem;margin-left: 0.1rem;">Q4: 礼品兑换中的手机充值卡兑换，怎么样进行</p>
    <p  style="font-size: 0.17rem;margin-top: 0.15rem;margin-left: 0.1rem;">充值，充值之前会和我电话确认嘛？</p>
    <p style="color: gray;font-size: 0.145rem;margin-top: 0.15rem;margin-left: 0.1rem;line-height:0.2rem;">不会，手机充值卡兑换，是直接充值到您填写的手机号上，充值之前不会和您电话确认，所以您在填写电话的时候，请确认电话是否正确。</p>
<p style="height: 2.8rem"></p>
    
    
  </div>
</template>

<script>
    export default {
        name: "integral_description",
      data(){
          return{

          }
      },
      methods:{
        onClickLeft(){
          this.$router.push({path:"/miintegral"})
        }
      }
    }
</script>

<style scoped>
  .van-nav-bar{
    background-color: rgb(49,144,232);
  }
  .van-nav-bar__title {
    color:white;
    font-weight: 700;
    font-size: 0.18rem;
  }
  :before{
    color: white;
    font-size: 0.25rem;
  }


</style>



