<!--我的余额-->
<template>
  <div class="top" style="position: relative">
    <van-nav-bar
      title="我的余额"
      left-arrow @click-left="onClickLeft"/>
    <div style="width: 96%;height: 1.7rem;position: absolute;top: 0.6rem;left:0.07rem;background-color: white;z-index: 2;border-radius: 0.03rem">
    <span style="font-size: 0.13rem;position: absolute;top: 0.1rem;left: 0.1rem;">当前余额</span><span><img src="data:image/png;base64," style="position: absolute;left: 2.8rem;top: 0.1rem;width: 0.14rem"/></span><span style="color: cornflowerblue ;position: absolute;right: 0.1rem;font-size: 0.13rem;top: 0.1rem;" @click="kaka">余额说明</span>
    </div>
    <span style="position: absolute;top: 1rem;z-index: 5;left: 0.2rem;font-size: 0.4rem">0.00</span><span style="position: absolute;left: 1.1rem;top: 1.15rem;z-index: 5;">元</span>
    <button style="width: 90%;height: 0.5rem;border: 0;color: white;border-radius: 0.05rem;position: absolute;top: 1.7rem;z-index: 5;left: 0.18rem;background-color: darkgray;">提现</button>
    <p style="font-size: 0.14rem;color: gray;position: absolute;top: 2.6rem;left: 0.15rem">交易明细</p>
    <img src="data:image/png;base64," style="width:2rem;height:1.2rem;position: absolute;left: 0.9rem;top: 3rem;"/>
    <p style="position: absolute;left: 1.4rem;top: 4.3rem;font-size: 0.16rem;color: gray">暂无明细记录</p>
  </div>
</template>

<script>
  export default {
    name: "balance",
    methods:{
      onClickLeft(){
        this.$router.go(-1);
      },
      kaka(){
        this.$router.push({path:"/statement"})
      }
    }
  }
</script>

<style >
  .van-nav-bar{
    background-color: rgb(49,144,232);
    height: 2.38rem;
    /*line-height: 0.9rem;*/
  }
  .van-nav-bar__title {

    color:white;
    font-weight: 700;
    font-size: 0.2rem;
  }
  :before{
    color: white;
    font-size: 0.28rem;
    line-height: 4.2rem;

  }
</style>



