<template>
  <div class="fuwu_home" v-if="yemian_if==='home'">
    <all_head>
      <template v-slot:one>
        <p class="qyh p1" @click="aa">&#xe65d;</p>
      </template>
      <template v-slot:two>
        <p class="p2">服务中心</p>
      </template>
    </all_head>
    <div class="service">
      <div class="service_left">
        <div class= "qyh">&#xe64c;</div>
        <p>在线客服</p>
      </div>
      <div class="service_left">
        <div class="qyh">&#xe64c;</div>
        <p>在线客服</p>
      </div>
      <div style="clear: both;"></div>
    </div>
    <p class="question">热门问题</p>
    <ul>
      <li v-for="(once,k) in dataArr1" :key="k" @click="btn_jump(once[0],once[1])">
        <span class="span_left">{{once[1]}}</span>
        <span class= "qyh span_right">&#xe625;</span>
        <div style="clear: both"></div>
      </li>
    </ul>
  </div>
  <div class="fuwu_dapian" v-else-if="yemian_if==='dapian'">

  </div>
  <div class="other" v-else></div>
</template>

<script>
  import Vue from 'vue';
  import All_head from "../../../all_head";
  export default {
    name: "fuwu_home",
    components: {All_head},
    data(){
      return{
        yemian_if:'home',
        dataArr1:[],
      }
    },
    methods:{
      aa(){
        this.$router.go(-1);
      },
      btn_jump(k,k2){
        this.$router.push({path:'/fuwu_once',query:{text:k,name:k2}});
      }
    },
    created: function () {
      Vue.axios.get('https://elm.cangdu.org/v3/profile/explain', {}).then((result) => {
        //后台传来的值存在result的data里面
        let c = [];
        for (let k in result.data) {
          c.push(result.data[k]);
        }
        for (let i = 0; i < 12; i++) {
          let a = [];
          a = c.splice(0, 2);
          this.dataArr1.push(a);
        }
        this.dataArr1.push([c[0][1], '优惠说明']);
        c.splice(0, 1);
        while (c.length > 0) {
          let a = [];
          a = c.splice(0, 2);
          this.dataArr1.push(a);
        }
      })
    }
  }
</script>

<style scoped>
  .service{
    padding-top: 0.48rem;
    width: 100%;
    height: 1.4rem;
    border-bottom: 2px solid rgba(0,0,0,0.03);
    text-align: center;
  }
  .service_left{
    width: 50%;
    float: left;
    padding-top: 0.23rem;
    border-left: 2px solid rgba(0,0,0,0.03);
  }
  .service_left p{
    margin-top: 0.12rem;
    font-size: 0.14rem;
  }
  .question{
    border-bottom: 2px solid rgba(0,0,0,0.03);
    height: 0.65rem;
    line-height: 0.65rem;
    padding-left: 0.16rem;
    font-size: 0.18rem;
  }
  ul li{
    border-bottom: 2px solid rgba(0,0,0,0.03);
    font-size: 0.14rem;
    color: #666;
    padding: 0 0.16rem;
  }
  .span_left{
    line-height: 0.5rem;
    float: left;
  }
  .span_right{
    float: right;
    line-height: 0.5rem;
  }
  .p1{
    font-size: 0.27rem;
    margin-left: -0.07rem;
  }
  .p2{
    font-size: 0.2rem;
    font-weight: 900;
  }
</style>



