<template>
  <div class="spHome4">

    <div class="dl clearfix" @click="btn_data_mi">
      <p class=" qyh pull-left touxiang">&#xe61b;</p>
      <p class="p1 ">登录/注册</p>
      <p class="qyh pull-right">&#xe601;</p>
      <p class="p2 ">咱无绑定手机号</p>
    </div>

    <div class="two clearfix">
      <div class="pull-left">
        <p class="num"><span class="num_ss">0.00</span>元</p>
        <p class="text" @click="hi">我的余额</p>
      </div>
      <div class="pull-left">
        <p class="num"><span class="num_dd">3</span>个</p>
        <p class="text" @click="pei">我的优惠</p>
      </div>
      <div class="pull-left">
        <p class="num"> <span class="num_ff">0</span>分</p>
        <p class="text" @click="sha">我的积分</p>
      </div>
    </div>


    <div class="three">
      <div class="dingdan clearfix" @click="dingdan">
        <p class="qyh pull-left">&#xe7ae;</p>
        <div class="pull-left">我的订单</div>
        <span class="qyh pull-right">&#xe601;</span>
      </div>
      <div class="jifen clearfix" @click="jifen">
        <p class="qyh pull-left">&#xe7ae;</p>
        <div class="pull-left">积分商城</div>
        <span class="qyh pull-right">&#xe601;</span>
      </div>
      <div class="huiyuan clearfix" @click="huiyuan">
        <p class="qyh pull-left">&#xe7ae;</p>
        <div class="pull-left">饿了吗会员卡</div>
        <span class="qyh pull-right">&#xe601;</span>
      </div>
    </div>
    <div class="four">
      <div @click="fuwu" class="fuwu clearfix" >
        <p class="qyh pull-left">&#xe7ae;</p>
        <div class="pull-left" >服务中心</div>
        <span class="qyh pull-right">&#xe601;</span>
      </div>
      <div @click="xiazai" class="xiazai clearfix">
        <p class="qyh pull-left">&#xe7ae;</p>
        <div class="pull-left">下载饿了吗</div>
        <span class="qyh pull-right">&#xe601;</span>
      </div>
    </div>



  </div>
</template>

<script>
  import tatle from '../../router/jianting';
    export default {
      name: "spHome4",
      created(){
        tatle.$emit('changecolor','我的');
      },
      methods:{
        fuwu(){
          this.$router.push('/fuwu_home');
        },
        xiazai(){
          this.$router.push('/download');
        },
        btn_data_mi(){
          this.$router.push('/data_mi');
        },
        hi(){
          this.$router.push({path:"/balance"})
        },
        sha(){
          this.$router.push({path:"/miintegral"})
        },
        pei(){
          this.$router.push({path:"/HongBao"})
        },
        dingdan(){
          this.$router.push({path:"/HongBao"})
        },
        jifen(){
          this.$router.push({path:"/integral"})
        },
        huiyuan(){
          this.$router.push({path:"/member"})
        }
      },
    }
</script>

<style scoped lang="less">
  .spHome4{
    padding-top: 0.48rem;
    .dl{
      background: #3190e8;
      padding: 0.1rem 0.1rem;
      .touxiang{
        color: white;
        font-size: 0.7rem;
        /*display: inline-block;*/
      }
      .p1{
        margin-top: 0.2rem;
        text-indent: 0.5em;
      }
      .p2{
        margin-top: 0.1rem;
        text-indent: 0.5em;
      }
    }
    .two{
      width: 100%;
      div{
        text-align: center;
        width: 33.3%;
        height: 1rem;
        background-color: white;
        .num{
          padding-top: 0.2rem;
          .num_ss{
            color: orange;
            font-size: 0.28rem;
          }
          .num_dd{
            color: red;
            font-size: 0.28rem;
          }
          .num_ff{
            color: greenyellow;
            font-size: 0.28rem;
          }
        }
        .text{
          padding-top: 0.1rem;
          font-size: 0.14rem;
          font-weight: 400;
        }
      }
    }
    .three{
      margin-top: 0.1rem;
      background-color: white;
      .dingdan{
        padding: 0.1rem;
        border-bottom: 1px rgba(0,0,0,0.1) solid;
      }
      .jifen{
        padding: 0.1rem;
        border-bottom: 1px rgba(0,0,0,0.1) solid;
      }
      .huiyuan{
        padding: 0.1rem;
      }
    }
    .four{
      width: 100%;
      margin-top: 0.1rem;
      background-color: white;
      .fuwu{
        padding: 0.1rem 0.1rem;
        border-bottom: 1px rgba(0,0,0,0.1) solid;
      }
      .xiazai{
        padding: 0.1rem 0.1rem;
        background-color: white;
      }
    }
  }
</style>



