const pro1 = {
  state:{
    //声明所有数据
    products:'祁远航',
    shopCartObj:{
      3269:{//店铺
        1648:{
          name:'',
          count:2,
          restaurant_id:3209,//热门分类
          id1:{
            num:1,
            price:20,
            name:'',
          },
          id2:{
            num:1,
            price:20,
            name:''
          },
        }
      }
    },
  },
  getters: {

  },
  mutations:{
    //增加商品
    addShop(state,a,b,name,restaurant_id,id,price,name1){
      if (!state.shopCartObj[a]) {
        state.shopCartObj[a]={}
      }
      if (!state.shopCartObj[a][b]) {
        state.shopCartObj[a][b]={restaurant_id:0,name:'',count:0}
      }
      state.shopCartObj[a][b].restaurant_id=restaurant_id;
      state.shopCartObj[a][b].name=name;
      state.shopCartObj[a][b].count+=1;
      if (!state.shopCartObj[a][b][id]) {
        state.shopCartObj[a][b][id]={num:0, price:0, show:'',}
      }
      state.shopCartObj[a][b][id].num+=1;
      state.shopCartObj[a][b][id].price=price;
      state.shopCartObj[a][b][id].name=name1;
    },
    //减少商品
    subtractShop(state,b,c,id){
      state.shopCartObj[b][c][id].num--;
      state.shopCartObj[b][c].count--;
      if (state.shopCartObj[b][c].count === 0) {
        delete state.shopCartObj[b][c];
      }
    },
    //清空购物车
    deleteShop(state,a){
      state[a]={};
    },
  },
  actions:{
    addShop2(context,v){
      context.commit('addShop',v);
    }
  },
};


export default pro1;



