<!--在线支付-->
<template>

  <div class="top">
    <van-nav-bar title="在线支付" left-arrow  @click-left="onClickLeft"/>
   <div style="width: 100%;height: 1.5rem;position: relative">
     <p style="font-size: 0.14rem;color: gray;position: absolute;left: 1.4rem;top: 0.5rem">支付剩余时间</p>
     <van-count-down
       :time="time"
       format="00 : mm : ss "
       style="font-size: 0.35rem;position: absolute;top: 0.8rem;left: 0.9rem"
     />
 <div style="width: 100%;height: 0.4rem;line-height: 0.4rem;background-color: rgb(242,242,242);position: absolute;top: 1.5rem;">
   <p style="background-color: rgb(242,242,242);margin-left: 0.2rem;color: gray">选择支付方式</p>
 </div>
     <div>
   </div>
   </div>
    <div>

      <div style="background-color: white;white:100%;height: 1.8rem">
        <img src="../assets/img/38.png" style="position: absolute;top: 2.55rem;left: 0.2rem;width: 0.45rem"/>
        <span style="position: absolute;top: 2.7rem;left: 0.7rem;color: gray">支付宝</span>
        <div style="width: 0.21rem;height: 0.21rem;border-radius: 50%;background-color: gainsboro;position: absolute;left: 3.4rem;top: 2.7rem;">
          <van-icon name="success" />
        </div>
        <p style="width: 100%;border-bottom: 0.01rem solid gainsboro;height: 0.001rem;position: absolute;top: 3.1rem;"></p>
        <img src="../assets/img/37.png"style="position: absolute;top: 3.25rem;left: 0.2rem;width: 0.45rem"/>
        <span style="position: absolute;top: 3.4rem;left: 0.75rem;color: gray">微信</span>
        <div style="width: 0.21rem;height: 0.21rem;border-radius: 50%;background-color: gainsboro;position: absolute;left: 3.4rem;top: 3.4rem;">
        <van-icon name="success" />
          </div>
        </div>
      <button style="background-color: rgb(76,217,100);border: 0px;width: 85%;height: 0.4rem;font-weight: 700;color: white;position: absolute;top: 4rem;;left: 0.3rem;border-radius: 0.05rem">
        确认支付
      </button>
    </div>
  </div>
</template>

<script>
  import { CountDown } from 'vant';

    export default {
        name: "payment",
      data(){
        return{
          time:  15*60 * 1000
        }
      },
      methods:{
        onClickLeft(){
          this.$router.push({path:"/member"});
        }
      }
    }
</script>

<style scoped>
  .van-nav-bar__title{
    color: white;
    font-weight: 700;
    font-size: 0.2rem;
  }
  .van-nav-bar{
    background-color: rgb(49,144,232);
  }
  :before{
    color: white;
    font-size: 0.2rem;
    font-weight: 700;
  }
  #head_top[data-v-cec0c0c0] {
    position: fixed;
    z-index: 100;
  }
</style>



